import { ResponsivePie } from '@nivo/pie'
import { Fragment, useEffect, useState } from 'react'
import { filePie } from '../../util/filePie'


type props={  
file:any
filter:any
app:Boolean

}

const PieChart = (props:props) => {
const [data,setData]=useState(null)


//make the file pie ready using filePie from util taking the file and filter(packets||ipSize) as prameters and set it to data state
 useEffect(() => {
        const file:any=filePie(props.file,props.filter,props.app);   
setData(file)
 },[filePie,props])



return(
    <Fragment>
    {data &&  <ResponsivePie
        data={data}
        margin={{ top: 40, right:100, bottom: 80, left: 100 }}
        innerRadius={0.4}
        padAngle={1}
        cornerRadius={3}
        arcLabelsTextColor={'#333'}
        borderWidth={1}
        borderColor={'#999999'}
        arcLabel={(datum)=>(datum.value+'%' )}
        arcLinkLabel={(d) => (`${d.label}`)}
        arcLinkLabelsColor={(datum)=>(datum.color)}
        arcLinkLabelsOffset={8}
        arcLinkLabelsTextOffset={0.4}
        arcLabelsSkipAngle={5}
        colors={{ scheme: 'nivo' }}

        tooltip={({datum}) => (
            <div
            key={datum.id}
            style={{
             padding: 12,
             color:'white',
            background: datum.color,
            }}
            >

            
             <strong>{datum.label}</strong>
            <br/>
            <strong>{props.filter}:{datum[props.filter]} <span>({datum.value}%)</span></strong>
            </div>
            )}
    theme={{tooltip: {container: {background: 'transperent',},},}}     
    />
}
</Fragment>

)}


export default PieChart;