import  { Fragment, useState,useRef,useContext,useEffect } from 'react'
import { FileCtx } from '../../ctx/file-ctx';
import { useHttp } from '../../hooks/http-hook';
//import {fastCsvFileToArray,FastReadAllFiles } from '../../util/fileHandlers';
// import { readPortList } from '../../util/readPortList';

type Props={
  fileArray:(fileArray:any)=> void,
  intf?:Boolean
}

const FileInput = (props:Props) => {
    const [formOk,setFormOk]=useState(true)
    const [file, setFile] = useState<any>();
    const [fileName, setFileName] = useState<any>();
    const {sendRequest,error,isLoading,clearError}=useHttp()
    let fileCtx=useContext(FileCtx)
    const inputRef = useRef<HTMLInputElement>(null);
  
     //when file changes re render and set filename to the input text  
      useEffect(() => {
        const fileChange = () => {
          if (!file) {
          return;
          }
          if (file.length > 1) {
              setFileName(`${file[0].name} +${file.length -1} more files`) 
          } else {
              setFileName(file.name);
          }
        };
        fileChange();
      }, [file]);

//click handler for perent div to click its input child      
const clickHandler= ()=>{
    inputRef.current?.click()
}


//when form is submitted handle file/files and set file to context
    const submitHandler=async (e:any)=>{
   e.preventDefault()

   //one file handler
   props.fileArray(file)
   if (!file.length) {
    const formData= new FormData()
      formData.append("file",file)
      const {fileArr}=await sendRequest(`http://localhost:8087/flow-analyzer/analyze`,'POST',formData)
    fileCtx.setIsLoading(true)   
        fileCtx.setFile(fileArr)
     };
   
   //multiple file handler 
   if (file.length>1) {
    fileCtx.setIsLoading(true)
  const formData= new FormData()
  for (let i = 0; i < file.length; i++) {
    formData.append("file", file[i]);
  }
  const {fileArr}=await sendRequest(`http://localhost:8087/flow-analyzer/analyze`,'POST',formData)
  console.log(fileArr);
      fileCtx.setFile(fileArr);  
   }
    }


//picked file handler 
   const onFileChange =async (event:any) => {
    let pickedFile;
      if (event.target.files && event.target.files.length === 1) {
        pickedFile = event.target.files[0];
        setFile(pickedFile);
        setFormOk(false);
    }else if (event.target.files && event.target.files.length > 1) {
      let readers:any = [];
      for (const key in event.target.files) {
        if (Object.hasOwnProperty.call(event.target.files, key)) {
          const element = event.target.files[key];
          readers.push(element);
        }
      }
      pickedFile = event.target.files;
      setFile(readers);
      setFormOk(false);
      if(props.intf){
        const formData= new FormData()
        for (let i = 0; i < readers.length; i++) {
          formData.append("file", readers[i]);
        }
        const {portList}=await sendRequest(`http://localhost:8087/flow-analyzer/intf`,'POST',formData)
         fileCtx.setPortList(portList)
         setFile(null)
         setFileName(null)
       }

    }else{
        setFormOk(true)
        setFile(null)
       }
      
 
};


  return (
<Fragment>
<form className='form' onSubmit={submitHandler} action="/import-csv" method="post" encType="multipart/form-data" >
  <div className='file-select' onClick={clickHandler}>
<div className="file-select-button" >{props.intf?'Upload Interfaces':"Select File"}</div>
    <div className="file-select-name">{file ? fileName : "No file chosen..."}</div> 
    <input multiple className='file-input' ref={inputRef}   name='file' id="file" type="file" accept='.csv,.txt' onChange={onFileChange}  />
    </div>
   {!props.intf&& <button className='analyze-btn'   type="submit" disabled={formOk}>Anlayze</button>}
    </form>
    </Fragment>
  )
}

export default FileInput