export const filterFile = (array, sender, port, protocol) => {
  let talkers;
  if (array.length > 0) {
    if (sender === "504" && port === "1000" && protocol === "504") {
      talkers = array.filter((row) => {
        return row.ipSender && row.outputPort1 && parseInt(row.ipProtocol) > 0;
      });
    } else if (sender === "504" && port !== "1000" && protocol === "504") {
      talkers = array.filter((row) => {
        return row.outputPort1 === port;
      });
    } else if (sender === "504" && port === "1000" && protocol !== "504") {
      talkers = array.filter((row) => {
        return row.ipProtocol === protocol;
      });
    } else if (sender !== "504" && port === "1000" && protocol === "504") {
      talkers = array.filter((row) => {
        return row.ipSender === sender;
      });
    } else if (sender !== "504" && port === "1000" && protocol !== "504") {
      talkers = array.filter((row) => {
        return row.ipSender === sender && row.ipProtocol === protocol;
      });
    } else if (sender !== "504" && port !== "1000" && protocol === "504") {
      talkers = array.filter((row) => {
        return row.ipSender === sender && row.outputPort1 === port;
      });
    } else if (sender === "504" && port !== "1000" && protocol !== "504") {
      talkers = array.filter((row) => {
        return row.outputPort1 === port && row.ipProtocol === protocol;
      });
    } else if (sender !== "504" && port !== "1000" && protocol !== "504") {
      talkers = array.filter((row) => {
        return (
          row.ipSender === sender &&
          row.outputPort1 === port &&
          row.ipProtocol === protocol
        );
      });
    }

    return talkers;
  } else return;
};

