//format line to nivo pie chart data
function PieFile(file, filter, totalArray, app) {
  let rows = [];
  file.map((row) => {
    return rows.push({
      id: row.id,
      label: app
        ? `${row.srcIp}:${row.srcPort}-${row.dstIp}:${row.dstPort}`
        : `${row.srcIp}-${row.dstIp}`,
      color: `${randColor()}`,
      value: parseFloat(((row[filter] / totalArray) * 100).toFixed(2)),
      [filter]: row[filter],
    });
  });

  return rows;
}

//randome color for pie slices
const randColor = () => {
  const randomColor = Math.floor(Math.random() * 360);
  return `hsla(${randomColor}, 60%, 55%, 1)`;
};

//iterating each line in file keep the 10 biggest and reformat it
export const filePie = (file, filter, app) => {
  let array = [];
  let restArr = [];
  for (let index = 0; index < file.length; index++) {
    const element = file[index];
    if (index <= 9) {
      array.push(element);
    } else {
      restArr.push(element);
    }
  }
  const totalArray = file.reduce(
    (accumulator, currentValue) => accumulator + parseInt(currentValue[filter]),
    0
  );

  const totalIps = restArr.length;
  const totalFilter = restArr.reduce(
    (accumulator, currentValue) => accumulator + parseInt(currentValue[filter]),
    0
  );

  let pieArray = PieFile(array, filter, totalArray, app);

  const restObj = {
    id: `${totalIps} other connections`,
    lable: `${totalIps} other connections`,
    color: `${randColor()}`,
    srcPort: app ? "" : "",
    dstPort: app ? "" : "",
    value: parseFloat(((totalFilter / totalArray) * 100).toFixed(2)),
    [filter]: totalFilter,
  };

  pieArray.push(restObj);

  return pieArray;
};
