import { comparePackets } from "./mostCommon";

//seperates obj to array and sort it by num of packets
const separator = (obj, sortFn, totalArray) => {
  const res = [];
  const keys = Object.keys(obj);

  keys.forEach((key) => {
    res.push({
      ...obj[key],
    });
  });
  res.sort(sortFn);
  const idArray = res.map((obj, i) => {
    return {
      id: `${i + 1}`,
      value: parseFloat(((obj.packets / totalArray) * 100).toFixed(2)),
      ...obj,
    };
  });

  return idArray;
};

//filter data by protocol and format data by top srcIps sorted by packet number
export const topTalkers = async (array, sender, protocol) => {
  let talkers = [];
  if (array.length > 0) {
    if (protocol === "504" && sender === "504") {
      const filteredArray = array.filter(
        (row) => row.ipSender && parseInt(row.ipProtocol) > 0
      );
      filteredArray.map((row) => {
        return talkers.push({
          ip: row.srcIp,
          packets: row.packets,
          ipSize: row.ipSize,
          protocol: row.ipProtocol,
          ipSender: row.ipSender,
          count: row.count,
        });
      });
    } else if (protocol !== "504" && sender === "504") {
      const filteredArray = array.filter((row) => row.ipProtocol === protocol);
      filteredArray.map((row) => {
        return talkers.push({
          ip: row.srcIp,
          packets: row.packets,
          ipSize: row.ipSize,
          protocol: row.ipProtocol,
          ipSender: row.ipSender,
          count: row.count,
        });
      });
    } else if (protocol === "504" && sender !== "504") {
      const filteredArray = array.filter((row) => row.ipSender === sender);
      filteredArray.map((row) => {
        return talkers.push({
          ip: row.srcIp,
          packets: row.packets,
          ipSize: row.ipSize,
          protocol: row.ipProtocol,
          ipSender: row.ipSender,
          count: row.count,
        });
      });
    } else if (protocol !== "504" && sender !== "504") {
      const filteredArray = array.filter(
        (row) => row.ipSender === sender && row.ipProtocol === protocol
      );
      filteredArray.map((row) => {
        return talkers.push({
          ip: row.srcIp,
          packets: row.packets,
          ipSize: row.ipSize,
          protocol: row.ipProtocol,
          ipSender: row.ipSender,
          count: row.count,
        });
      });
    }

    const totalArray = talkers.reduce(
      (accumulator, currentValue) =>
        accumulator + parseInt(currentValue.packets),
      0
    );

    const countedIp = await talkers.reduce((acc, obj, i) => {
      // const key = obj.ip;
      // const curGroup = acc[key] ?? [];
      // if (curGroup.count > 0) {
      //   obj.packets += curGroup.packets;
      //   obj.ipSize += curGroup.ipSize;
      //   obj.count += curGroup.count;
      //   return { ...acc, [key]: obj };
      // } else {
      //   return { ...acc, [key]: obj };
      // }

      const item = i > 0 && acc.find(({ ip }) => ip === obj.ip);
      if (item) {
        item.packets += obj.packets;
        item.ipSize += obj.ipSize;
        item.count += obj.count;
      } else if (acc) {
        acc.push({ ...obj });
      }
      return acc;
    }, []);

    const topTalkersList = separator(countedIp, comparePackets, totalArray);

    return topTalkersList;
  } else return;
};
