import  { useContext, useState, Fragment,useEffect} from 'react';

import '../App.css';
import FileInput from '../components/UI/FileInput';

import { FileCtx } from '../ctx/file-ctx';

import { mostCommonTalkers } from '../util/mostCommon';
import { topTalkers } from '../util/topOnce';

 import LoadingSpinner from '../components/UI/LoadingSpinner';
import Filter from '../components/UI/Filter';
import NavBar from '../components/UI/NavBar';
import { filterFile } from '../util/filterFile';
import Pie from '../components/UI/pie/Pie';
import Tables from '../components/UI/tables/Tables';
import IntfModal from '../components/UI/IntfModal';


  const Dash = () => {
  let {file,protocolArray,isLoading,setIsLoading,portList}=useContext(FileCtx)
  const [sender,setSender]=useState<any>("504")
  const [port,setPort]=useState<any>("1000")
  const [protocol,setProtocol]=useState<any>('504')

  const [newFile,setNewFile]=useState<any>(null)
  const [fileNameArray,setFileNameArray]=useState<any>(null)
  const [newBarFile,setNewBarFile]=useState<any>(null)
  const [appFile,setAppFile]=useState<any>(null)
  const [filteredFile,setFilteredFile]=useState<any>()
  const [filteredBarFile,setFilteredBarFile]=useState<any>(null)
  const [filteredAppFile,setFilteredAppFile]=useState<any>(null)



//let tableAppFile:any=[];
  
function formatDate(date:Date) {
  function padTo2Digits(num:any) {
    return num.toString().padStart(2, '0');
  }
  return (
    [
      padTo2Digits(date.getDate()),
      padTo2Digits(date.getMonth()+1),
      padTo2Digits(date.getFullYear()),
    ].join('/') +
    ' ' +
    [
      padTo2Digits(date.getHours()),
     padTo2Digits(date.getMinutes()),
    ].join(':')
  );
}


useEffect(()=>{
  const init=async()=>{
    //console.log(file);
    
    if(!file){
      setIsLoading(false)
      setNewFile(null)
      setAppFile(null)
      setFilteredAppFile(null)
      setNewBarFile(null)
      setFilteredBarFile(null)
      setFilteredFile(null)
      setPort('1000')
      setProtocol('504')
      setSender('504')
    }
      if(file&&!newFile&&!newBarFile){ 
        setIsLoading(true)
        await Promise.all([ 
          (async () => (setNewFile(await mostCommonTalkers(file,false))))(),
          (async () => (setAppFile(await mostCommonTalkers(file,true))))(),
          (async () => (setNewBarFile( await topTalkers(file,sender,protocol))))(), 
        ]).then(()=>{ 
          setFilteredFile(newFile)
          setFilteredBarFile(newBarFile)
          setFilteredAppFile(appFile)
          setIsLoading(false)
        });
        
       
        
      }
      if(newFile&&newBarFile&&appFile&&!filteredFile&&!filteredBarFile){
        
        setFilteredFile(newFile)
        setFilteredBarFile(newBarFile)
        setFilteredAppFile(appFile)
        setIsLoading(false)
      }
      
  }
   init()
  
},[file,newFile,newBarFile,appFile,port,protocol,sender])


   //when file first loads create graph ready arrays of objects

  const onFilter=(senders:any,ports:any,protocols:any)=>{
    
    if(ports==='1000'&&protocols==='504'&&senders==='504'){
      setFilteredFile(newFile)
      setFilteredBarFile(newBarFile)
      setFilteredAppFile(appFile)
      setIsLoading(false)
    }
else if(ports!=='1000'&&protocols==='504'&&senders==='504'){
  setIsLoading(true)
  Promise.all([
    (async () => (setFilteredFile(await filterFile(newFile,senders,ports,protocols))))(),
    (async () => (setFilteredAppFile(await filterFile(appFile,senders,ports,protocols))))(),
  (async () => (setFilteredBarFile(filteredBarFile)))(), 
]).then(()=>{setIsLoading(false)}); 
}else if(ports!=='1000'||protocols!=='504'||senders!=='504'){
  setIsLoading(true)
   Promise.all([
    (async () => (setFilteredFile(await filterFile(newFile,senders,ports,protocols))))(),
    (async () => (setFilteredAppFile(await filterFile(appFile,senders,ports,protocols))))(),
  (async () => (setFilteredBarFile( await topTalkers(file,senders,protocols))))(), 
]).then(()=>{setIsLoading(false)});  
}

setPort(ports)
setProtocol(protocols)
setSender(senders)
setIsLoading(false)
  }

  //closes the watcher and switch inputs
//  const clickHandler=(e:any)=>{
//   e.preventDefault()
//   if(watcher){
//     watcher.close()
//     setWatcher(null)
//     setIsFile(!isFile)
    
//   }
// else{
// setIsFile(!isFile)

// }
//  }

const fileArrayHandler=(fileArray:any)=>{
if(!fileArray.length){
  const lastDate=new Date(fileArray.lastModified)

  const name = fileArray.name +' ( '+formatDate(lastDate)+' )'
setFileNameArray(name)


}else if (fileArray.length>1){
 let fileBase=fileArray[0].name.split(/[-.]/)[0] 
 let fileNum:Array<string>=[];
 const lastDate=new Date(fileArray[0].lastModified)
 fileArray.map((file:any)=>{
return fileNum.push(file.name.split(/[-.]/)[1])
 }) 
 setFileNameArray(fileBase+' ('+fileNum+') '+ formatDate(lastDate));
}
}

  return (
    <Fragment>
      {isLoading&& <LoadingSpinner/>}
    {!file&&  
    <div className='file-input'>
<a href="https://www.net-monitor.net" ><div className='logo'></div></a>
      <div className='form-container'>{!portList?<FileInput  fileArray={fileArrayHandler} intf   /> :<FileInput  fileArray={fileArrayHandler}   />   }</div> 
     {/* {!isFile &&<div className='form-container'>  <FolderInput    /> <button className='switch-btn' onClick={clickHandler}>change to file picker</button>  </div>} */}
     
    </div>
    }
    {file&&protocolArray.length&&
    <div className='dash'>
     <NavBar >
     <div className='files-arr'>{fileNameArray}</div>
      { portList?<IntfModal sender={sender}/>:<div className='files-arr'></div>}
</NavBar>
<Filter onFilter={onFilter} port={port} protocol={protocol} sender={sender} />

<div className='all-data'>
  {filteredFile&&<Pie filteredFile={filteredFile} filteredAppFile={filteredAppFile} /> }
  {filteredFile&&filteredBarFile&& <Tables appFile={filteredAppFile} filteredFile={filteredFile} filteredBarFile={filteredBarFile} />}
 </div> 
 
    </div>
    
    }
    <div className='ver'>Flow Analyzer v1.1.8(web)</div>
    </Fragment>
  );
};

export default Dash 