
import React from 'react'

import styles from './PieContainer.module.css'

type Props = {
    title:string
    children?: JSX.Element|JSX.Element[],
}

const PieContainer:React.FC<Props> = ({title,children}) => {
  return (
    <div className={styles.pie}>
           <h2>{title}</h2>
           {children}
           </div>
  )
}

export default PieContainer