
import {createColumns} from '../../../util/tableColomns'
import TableContainer from '../TableContainer'
import Table from './Table'

type Props = {
    filteredFile:any,
    filteredBarFile:any
    appFile:any
}

const Tables = (props: Props) => {
    const {columns,columnsAppTable,topColumns}=createColumns(props.filteredFile[0].isSflow)
  return (
    <>
    <TableContainer title='Ip Conversation' styling='table_connections' >
     <Table  columns={columns} data={props.filteredFile}/>
   
  </TableContainer>
  {props.appFile&& <TableContainer title="Top Apps" styling='table_connections'>
  <Table  columns={columnsAppTable} data={props.appFile}    />
    </TableContainer> } 

    {props.filteredBarFile&& <TableContainer styling='table_connections' title="Ip Host">
  <Table  columns={topColumns} data={props.filteredBarFile} />
    </TableContainer>}
    </>
  )
}

export default Tables