import React,{useState} from 'react'
import PieChart from '../../data/PieChart'
import PieContainer from '../PieContainer'
import TableContainer from '../TableContainer'
type Props = {
filteredFile:any
filteredAppFile:any
}



const Pie = (props: Props) => {
    const [pieFilter,setPieFilter]=useState('packets')

    //pie btn toggle handler
 const toggleHandler=(e:any)=>{
    e.preventDefault()
  if(pieFilter==='packets'){
    setPieFilter('ipSize')
  }
  if(pieFilter==='ipSize'){
    setPieFilter('packets')
  }}
  return (
    <TableContainer title='Top Conversation' styling='pie_container'>
      <button onClick={toggleHandler} className='toggle-btn'><span className={pieFilter==='packets'? 'bold' :''}>{props.filteredFile[0].isSflow?"Packets":'In Byte'}</span>/<span className={pieFilter==='ipSize' ? 'bold' :''}>{props.filteredFile[0].isSflow?"Ip Size":'Out Byte'}</span></button>
    <div className='pies'>
         <PieContainer title='Top Conversation'>
           <PieChart file={props.filteredFile} filter={pieFilter} app={false}/>
           
           </PieContainer>
           <PieContainer title='Top Apps'>
           <PieChart file={props.filteredAppFile} filter={pieFilter} app={true}/>
           </PieContainer>
           
    </div>
    </TableContainer>
  )
}

export default Pie