import React,{useContext} from 'react'
import { FileCtx } from '../../ctx/file-ctx';
import styles from './NavBar.module.css'
type Props = {
    children?:JSX.Element|JSX.Element[]
}




const NavBar:React.FC<Props> = ({children}) => {
const {setFile,setPortList}=useContext(FileCtx)



//resets the current file causing return to choose file
const clickfileHandler=(e:any)=>{
    setFile(null)
    setPortList(null)
    }


  return (
    <div className={styles.selector}>
        <div className={styles.logo_net}></div>
       <div className={styles.nav_btns}>
     {children}
<button onClick={clickfileHandler} className={styles.load_files}>Back</button>
</div>
</div>
  )
}

export default NavBar