//reduce object array to object with connection uniqe key count and sum of packets and ipSize
const reducer = (objectArray, isApp) => {
  if (!isApp) {
    return objectArray.reduce((acc, obj, i) => {
      const item =
        i > 0 &&
        acc.find(
          ({ srcIp, dstIp }) => srcIp === obj.srcIp && dstIp === obj.dstIp
        );
      if (item) {
        item.packets += obj.packets;
        item.ipSize += obj.ipSize;
        item.count += obj.count;
      } else {
        acc.push({ ...obj });
      }
      return acc;
    }, []);
  } else {
    return objectArray.reduce((acc, obj, i) => {
      const item = i > 0 && acc.find(({ key }) => key === obj.key);
      if (item) {
        item.packets += obj.packets;
        item.ipSize += obj.ipSize;
        item.count += obj.count;
      } else if (acc) {
        acc.push({ ...obj });
      }
      return acc;
    }, []);
  }
};

//sort array by packets
export function comparePackets(a, b) {
  if (a.packets > b.packets) {
    return -1;
  }
  if (a.packets < b.packets) {
    return 1;
  }
  return 0;
}

//seprate object to array of object and sort by sorting function
const separator = async (obj, totalArray) => {
  const sorted = await obj.sort(comparePackets);

  const idArray = await Promise.all(
    sorted.map((obj, i) => {
      // if (i <= 2) {
      //   const valueArr = await Promise.all([
      //     (async () => await checkIp(obj.srcIp))(),
      //     (async () => await checkIp(obj.dstIp))(),
      //   ]);

      //   return {
      //     id: `${i + 1}`,
      //     value: parseFloat(((obj.packets / totalArray) * 100).toFixed(2)),
      //     hostSrc: obj.srcIp + ' ' + valueArr[0],
      //     hostDst: obj.srcIp + ' ' + valueArr[1],
      //     ...obj,
      //   };
      // } else {
      return {
        id: `${i + 1}`,
        value: parseFloat(((obj.packets / totalArray) * 100).toFixed(2)),
        ...obj,
      };
      // }
    })
  );
  //console.log(idArray);
  return idArray;
};

//filter data array by sender and format data to uniqe connections
export const mostCommonTalkers = async (array, reducerFn) => {
  if (array.length > 0) {
    const totalArray = array.reduce(
      (accumulator, currentValue) =>
        accumulator + parseInt(currentValue.packets),
      0
    );

    const reducedObject = await reducer(array, reducerFn);
    console.log(reducedObject);
    const tableData = await separator(reducedObject, totalArray);
    console.log(tableData);
    return tableData;
  } else return;
};
